import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanAlertProvider } from 'src/providers/africlean-alert/africlean-alert.provider';
import { NavController } from '@ionic/angular';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanAdressComponent } from '../africlean-adress/africlean-adress.component';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';

@Component({
  selector: 'africlean-adress-display',
  templateUrl: './africlean-adress-display.component.html',
  styleUrls: ['./africlean-adress-display.component.scss'],
})
export class AfricleanAdressDisplayComponent implements OnInit {

  /**
   * Adresse à afficher
   */
  @Input() public adress: {
    adresseId: string,
    idHome: string,
    country: string,
    region: string,
    commune: string,
    district: string,
    sector: string,
    home: string,
    state?: string
  };

  /**
   * Theme lié a l'adresse
   */
  @Input() public theme: string;

  /**
   * Rang de l'adresse
   */
  @Input() public rank: number;

  /**
   * Affichage du bouton
   */
  @Input() public displayButton: boolean;

  /**
   * Affichage du checkbox
   */
  @Input() public displayCheckbox: boolean;

  /**
   * Emet le homeUserId de l'adresse
   */
  @Output() public emitAdressId = new EventEmitter<string>();

  /**
   * Emet le home code
   */
  @Output() public emitHomeCode = new EventEmitter<string>();


  constructor(private userProvider: AfricleanHttpUserProvider,
              private session: AfricleanSessionProvider,
              private alert: AfricleanAlertProvider,
              private navCtrl: NavController,
              private modal: AfricleanModalProvider,
              private observableProvider: AfricleanObservablesProvider) { }

  ngOnInit() {
  }

  /**
   * Choisit l'adresse comme un abonnement
   */
  public subscription() {
    this.emitHomeCode.emit(this.adress.idHome);
  }

  /**
   * Message de warning avant la suppresion d'une adresse
   */
  public delete() {
    const message = 'Attention vous allez supprimer définitivement cette adresse.';
    const css = '';
    const buttons = [
      {
        text: 'Supprimer',
        handler: () => {
                  this.deleteAdress();
        }
      },
      {
        text: 'Annuler',
        handler: () => {
        }
      }
    ];
    this.alert.alertMessage(message, css, buttons);
  }

  /**
   * Modifie une adresse
   */
  public async update() {

    // On appel la modal pour saisir une adresse
    const props = {
      displayButton: true,
      action: 'update'
    };
    await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);

    // On récupère les data
    const data = await this.modal.getData();

    // On met a jour l'adresse
    if (data) {
      const path = 'user/update-adress';
      const body = {
        homeCode: this.adress.idHome,
        nameOfHome: data.nameOfHome,
        sectorCode: data.sectorCode
      };
      this.userProvider.updateAdress(path, body).subscribe(
        (home) => {
          // recharge la liste des adresses
          this.observableProvider.loadAdresses.emit(true);
        }
      );
    }
  }

  /**
   * Supprime une adresse
   */
  private async deleteAdress() {
    const userId = await this.session.getItem('userId');
    const path = 'user/delete-adress/';
    if (this.adress.state === null || this.adress.state === 'RESILIEE') {
          const body = {
            userCode: userId,
            homeCode: this.adress.idHome
          };
          this.userProvider.deleteAdress(path, body).subscribe(
            (home) => {
              // tslint:disable-next-line: no-unused-expression
              home ? this.emitHomeCode.emit(home.objectId) : null;
            }
          );
    } else {
      this.popupAdressWithSubscribe();
    }
  }


  /**
   * Message de warning avant la supression d'une adresse liée à un abonement
   */
  private popupAdressWithSubscribe() {
        const message = 'Attention un abonnement est lié à cette adresse.';
        const css = '';
        const buttons = [
          {
            text: 'Se désabonner',
            handler: () => {
                      this.navCtrl.navigateForward('consult-abonnement');
            }
          },
          {
            text: 'Annuler',
            handler: () => {
            }
          }
        ];
        this.alert.alertMessage(message, css, buttons);
  }

}
