import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Platform, NavController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AfricleanConfigProvider } from 'src/providers/africlean-config/africlean-config.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { ActivatedRoute, RouterStateSnapshot  } from '@angular/router';
import { AfricleanWebsocketProvider } from 'src/providers/africlean-websocket/africlean-websocket.provider';
// import { AfricleanWebsocketProvider } from 'src/providers/africlean-websocket/africlean-websocket.provider';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private translate: TranslateService,
    private config: AfricleanConfigProvider,
    private session: AfricleanSessionProvider,
    private navCtrl: NavController,
    private route: ActivatedRoute
  ) {
    this.initializeApp();
  }

  initializeApp() {
      this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      this.configTranslate();
      this.setPlatform();
      this.session.isExist().then(
        (active) => {
          if (active) {
            // tslint:disable-next-line: no-string-literal
            const routeState: RouterStateSnapshot = this.route.snapshot['_routerState'];
            console.log('URL ROUTE PRECEDENT ' + routeState.url)
            this.navCtrl.navigateForward(routeState.url);
          }
        }
      );
    });
  }

  public setPlatform() {
    const platforms = this.platform.platforms();
    this.config.setPlatform(platforms[0]);
}

  /**
   * Configurer le service de traduction
   */
  public configTranslate() {
    this.translate.addLangs(['fr', 'en']);
    this.translate.setDefaultLang('fr');
  }

}
