import { Component, OnInit, Input } from '@angular/core';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanAdressComponent } from '../africlean-adress/africlean-adress.component';
import { AfricleanUserModel } from 'src/models/africlean-user.model';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';

@Component({
  selector: 'africlean-informations',
  templateUrl: './africlean-informations.component.html',
  styleUrls: ['./africlean-informations.component.scss'],
})
export class AfricleanInformationsComponent implements OnInit {

  /**
   * L'utilisateur qui se connecte
   */
  public user: AfricleanUserModel;

  /**
   * Permet d'activé ou de désactiver le formulaire
   */
  public disabled = true;

  /**
   * Permet de stocker les données à modifier
   */
  public data: {
    userId: string,
    phoneNumber: string,
    newPhoneNumber: string,
    role: string,
    password: string,
    lastname: string,
    firstname: string,
    email: string
  };

  constructor(private modal: AfricleanModalProvider, private session: AfricleanSessionProvider,
              private userProvider: AfricleanHttpUserProvider) { }

  ngOnInit() {

    // Récupérer le user session
    this.session.getItem('user').then(
      (user) => {
          this.user = user;
          this.session.getItem('role').then(
            (userRole) => {
              // initialisation de l'objet data
              this.data = {
                userId: this.user.objectId,
                phoneNumber: this.user.phoneNumber,
                newPhoneNumber: '',
                role: userRole,
                password: this.user.password,
                lastname: this.user.lastName,
                firstname: this.user.firstName,
                email: this.user.email
              };
            }
          );
      }
    );
  }

  /**
   * Affiche l'adresse de l'utilisateur
   */
  public getAdress() {
    this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css');
  }

  /**
   * Modifie les données
   */
  public update() {
    this.disabled = true;
    const url = 'user/update-informations';
    this.userProvider.updateInfos(url, this.data).subscribe(
      (userUpdated) => {
          // mettre à jour la session
          this.session.setItem('user', userUpdated);
          this.session.setItem('login', userUpdated.phoneNumber);
      }
    );
  }

  /**
   * Active les champs
   */
  public able() {
    this.disabled = false;
  }

  /**
   * recupère la valeur du champ
   * @param field champ
   * @param event évenement émis
   */
  getData(field: any, event) {
    this.data[field] = event.detail.value;
  }

}
