import { AfricleanHttpProvider } from './africlean-http.provider';
import { Observable } from 'rxjs';
import {AfricleanIncidentModel} from '../../models/intervention/africlean-incident.model';
import { Injectable } from '@angular/core';

@Injectable()
export class AfricleanHttpIncidentProvider extends AfricleanHttpProvider {

    /**
     * Récupère tous les incidents d'un utilisateur
     * @param url url
     * @param options options
     */
    public getIncidentByIdUser(url: string, options?: {}): Observable<AfricleanIncidentModel[]> {
        return this.get(url, options);
    }


    /**
     * Récupère tous les incidents
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<AfricleanIncidentModel[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre un incident
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<AfricleanIncidentModel[]> {
        return this.post(url, body, options);
    }


}
