
import { AfricleanHttpProvider } from './africlean-http.provider';
import { Observable } from 'rxjs';
import { AfricleanNotificationModel } from 'src/models/abonnement/africlean-notification.model';
import { Injectable } from '@angular/core';
import { AfricleanHttpNotificationUserProvider } from 'src/models/abonnement/africlean-http-notification-user.provider';

@Injectable()
export class AfricleanHttpNotificationProvider extends AfricleanHttpProvider {

    /**
     * Récupère toutes les notifications d'un utilisateur
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public getNotificationsByIdUser(url: string, body?: {}, options?: {}): Observable<{
        notification: AfricleanNotificationModel,
        state: boolean
    }[]> {
        return this.post(url, body, options);
    }


    /**
     * Récupère toutes les notifications
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<AfricleanNotificationModel[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre une notification
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<AfricleanNotificationModel[]> {
        return this.post(url, body, options);
    }

    /**
     * Permet de fermer une notification
     * @param url url
     * @param body corps de la méthode
     * @param options options
     */
    public hiden(url: string, body: {}, options?: {}): Observable<AfricleanHttpNotificationUserProvider> {
        return this.post(url, body, options);
    }

}
