import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'africlean-abo-payments',
  templateUrl: './africlean-abo-payments.component.html',
  styleUrls: ['./africlean-abo-payments.component.scss'],
})
export class AfricleanAboPaymentsComponent implements OnInit {

  /**
   * Expansion des paiements avenir
   */
  public expansionLaterPayement = false;

  /**
   * Expansion de l'historique des paiements
   */
  public expansionArchive = false;

  constructor() { }

  ngOnInit() {}

  /**
   * Etent ou non la partie paiment à venir
   */
  public toogleLaterPayement() {
    this.expansionLaterPayement = !this.expansionLaterPayement;
  }

  /**
   * Etent ou non la partie historique des paiments
   */
  public toogleArchive() {
    this.expansionArchive = !this.expansionArchive;
  }
}
