import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'africlean-small-button',
  templateUrl: './africlean-small-button.component.html',
  styleUrls: ['./africlean-small-button.component.scss'],
})
export class AfricleanSmallButtonComponent implements OnInit {
  
  /**
   * theme du bouton
   */
  @Input() theme: string;

  /**
   * titre du bouton
   */
  @Input() title: string;

  /**
   * Evenement émis lors du click du bouton
   */
  @Output() clickButton = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {}

}
