import { AfricleanHttpProvider } from './africlean-http.provider';
import { Observable } from 'rxjs';
import {AfricleanPlanningInterventionModel} from '../../models/intervention/africlean-planning-intervention.model';
import { Injectable } from '@angular/core';

@Injectable()
export class AfricleanHttpPlanningProvider extends AfricleanHttpProvider {


    /**
     * Récupère tous les Plannings
     * @param url url
     * @param options options
     */
    public getCountries(url: string, options?: {}): Observable<AfricleanPlanningInterventionModel[]> {
        return this.get(url, options);
    }


    /**
     * Récupère tous les Plannings
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<AfricleanPlanningInterventionModel[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre un Planning
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<AfricleanPlanningInterventionModel[]> {
        return this.post(url, body, options);
    }


}
