import { Component, OnInit, Input } from '@angular/core';
import { AfricleanIncidentModel } from 'src/models/intervention/africlean-incident.model';
import { AfricleanHttpIncidentProvider } from 'src/providers/africlean-http/africlean-http-incident.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';

@Component({
  selector: 'africlean-incidents-list',
  templateUrl: './africlean-incidents-list.component.html',
  styleUrls: ['./africlean-incidents-list.component.scss'],
})
export class AfricleanIncidentsListComponent implements OnInit {

  /**
   * Liste des incidents d'un utilisateurs
   */
  public incidents: AfricleanIncidentModel[];

  constructor(private incidentProvider: AfricleanHttpIncidentProvider,
              private session: AfricleanSessionProvider,
              private observableProvider: AfricleanObservablesProvider) { }

  ngOnInit() {
    this.getIncidents();
    this.observableProvider.loadIncident.subscribe(
      (loadIncident) => {
        if (loadIncident) {
          this.getIncidents();
        }
      }
    );
  }

  /**
   * Retourne tous les incidents liés à un user
   */
  public async getIncidents() {
      const idUser = await this.session.getItem('userId');
      const url = 'incident/user-incident/' + idUser;
      this.incidentProvider.getIncidentByIdUser(url).subscribe(
        (incidentsList) => {
            this.incidents = incidentsList.slice(0, 5);
        }
      );
  }

}
