import { Injectable, EventEmitter } from "@angular/core";
import { Observable, from  } from 'rxjs';
import { map } from 'rxjs/operators'

@Injectable()

/**
 * Service encapsulant l'ensemble des observables que l'application à besoin
 */
export class AfricleanObservablesProvider{

    /**
     * Observable qui emet un événement lorque la langue change
     */
    public translateEmiter: EventEmitter<string>;

    /**
     * Observable qui emet un événement lorque la valeur de l'input de type radio est renseigné
     */
    public inputRadioEmiter: EventEmitter<string>;

    /**
     * Emet lorsque un utilisateur est connecté
     */
    public isAuth: EventEmitter<boolean>;

    /**
     * Emet lorsque un utilisateur est fait une demande d'abonnement ou de résiliation
     */
    public subscriptionRequest: EventEmitter<boolean>;

    /**
     * Emet lorsque un utilisateur fait agit au niveau de ces adresses
     */
    public loadAdresses: EventEmitter<boolean>;

    /**
     * Emet lorsque un utilisateur fait agit au niveau des incidents
     */
    public loadIncident: EventEmitter<boolean>;


    constructor() {
        this.translateEmiter = new EventEmitter<string>();
        this.inputRadioEmiter = new EventEmitter<string>();
        this.isAuth = new EventEmitter<boolean>();
        this.subscriptionRequest = new EventEmitter<boolean>();
        this.loadAdresses = new EventEmitter<boolean>();
        this.loadIncident = new EventEmitter<boolean>();
    }
}