import { Component, OnInit, Input } from '@angular/core';
import { AfricleanIncidentModel } from 'src/models/intervention/africlean-incident.model';

@Component({
  selector: 'incident-item',
  templateUrl: './incident-item.component.html',
  styleUrls: ['./incident-item.component.scss'],
})
export class IncidentItemComponent implements OnInit {

  @Input() public incident: AfricleanIncidentModel;

  @Input() public theme: string;

  @Input() public incidentDate: string;

  constructor() { }

  ngOnInit() {
    this.customizeDate();
  }

  /**
   * Customise la date
   */
  private customizeDate() {
    const date = this.incident.incidentDate.toString().split('T')[0];
    const splitDate = date.split('-');
    this.incidentDate = splitDate[2] + '-' + splitDate[1] + '-' + splitDate[0];
  }
}
