import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { IonicStorageModule } from '@ionic/storage';

import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

import {TranslateModule, TranslateLoader } from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';


import { AfricleanTranslateProvider } from 'src/providers/africlean-translate/africlean-translate.provider';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';
import { AfricleanFilesManagerProvider } from 'src/providers/africlean-files-manager/africlean-files-manager.provider';
import { AfricleanDataProvider } from 'src/providers/africlean-data/africlean-data.provider';
import { AfricleanControlAuthenticationProvider } from 'src/providers/africlean-control-fields/africlean-control-authentication.provider';
import { AfricleanControlRegistrationProvider } from 'src/providers/africlean-control-fields/africlean-control-registration.provider';
import { AfricleanConfigProvider } from 'src/providers/africlean-config/africlean-config.provider';
import { ComponentsModule } from './components/components.module';
import { AfricleanAlertProvider } from 'src/providers/africlean-alert/africlean-alert.provider';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';
import { AfricleanPopoverProvider } from 'src/providers/africlean-popover/africlean-popover.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanHttpSubscribeProvider } from 'src/providers/africlean-http/africlean-http-subscribe.provider';
import { AfricleanHttpLocalisationProvider } from 'src/providers/africlean-http/africlean-http-localisation.provider';
import {AfricleanHttpPlanningProvider} from '../providers/africlean-http/africlean-http-planning.provider';
import {AfricleanHttpCommentProvider} from '../providers/africlean-http/africlean-http-comment.provider';
import {AfricleanHttpIncidentProvider} from '../providers/africlean-http/africlean-http-incident.provider';
import { AfricleanDateProvider } from 'src/providers/africlean-date/africlean-date.provider';

/**
 * Factory du loader http des fichiers de tradction
 * @param { HttpClient } httpClient client http angular 
 */
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient, './assets/i18n/', '.json');
}

import { SocketIoModule, SocketIoConfig } from 'ngx-socket-io';
import { AfricleanWebsocketProvider } from 'src/providers/africlean-websocket/africlean-websocket.provider';
import { AfricleanHttpNotificationProvider } from 'src/providers/africlean-http/africlean-http-notification.provider';
const config: SocketIoConfig = { url: 'http://localhost:3001', options: {} };

@NgModule({
  declarations: [AppComponent],
  entryComponents: [],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    ComponentsModule,
    IonicModule.forRoot(),
    IonicStorageModule.forRoot({
      name: '__mydb',
      driverOrder: ['indexeddb', 'sqlite', 'websql']
    }),
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
      }
    }),
    SocketIoModule.forRoot(config)
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AfricleanTranslateProvider,
    AfricleanObservablesProvider,
    AfricleanFilesManagerProvider,
    AfricleanDataProvider,
    AfricleanControlAuthenticationProvider,
    AfricleanControlRegistrationProvider,
    AfricleanConfigProvider,
    AfricleanAlertProvider,
    AfricleanModalProvider,
    AfricleanHttpUserProvider,
    AfricleanPopoverProvider,
    AfricleanSessionProvider,
    AfricleanHttpSubscribeProvider,
    AfricleanHttpLocalisationProvider,
    AfricleanHttpPlanningProvider,
    AfricleanHttpCommentProvider,
    AfricleanHttpIncidentProvider,
    AfricleanDateProvider,
    AfricleanWebsocketProvider,
    AfricleanHttpNotificationProvider,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
