import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AfricleanHttpProvider } from './africlean-http.provider';
import {AfricleanCommentModel} from '../../models/intervention/africlean-comment.model';
import {AfricleanUserModel} from '../../models/africlean-user.model';

@Injectable()
export class AfricleanHttpCommentProvider extends AfricleanHttpProvider {

    /**
     * Récupère un commentaire à partir de son identifiant
     * @param url url
     * @param options options
     */
    public getCommentById(url: string, options?: {}): Observable<AfricleanCommentModel> {
        return this.get(url, options);
    }

    /**
     * Récupère tous les commentaires
     * @param url url
     * @param options options
     */
    public getAll({ url, options }: { url: string; options?: {}; }): Observable<{
        author: AfricleanUserModel,
        comment: AfricleanCommentModel
    }[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre un commentaire
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<AfricleanCommentModel> {
        return this.post(url, body, options);
    }

    /**
     * Récupère la liste des auteurs
     * @param url url
     * @param options options
     */
    public getAllAuthor(url: string, options?: {}): Observable<AfricleanUserModel[]> {
        return this.get(url, options);
    }
}
