import { Component, OnInit, Input } from '@angular/core';
import { AfricleanAlertProvider } from 'src/providers/africlean-alert/africlean-alert.provider';
import {AfricleanHttpCommentProvider} from '../../../providers/africlean-http/africlean-http-comment.provider';
import { AfricleanCommentModel } from 'src/models/intervention/africlean-comment.model';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanDateProvider } from 'src/providers/africlean-date/africlean-date.provider';
import { AfricleanUserModel } from 'src/models/africlean-user.model';

@Component({
  selector: 'africlean-comments',
  templateUrl: './africlean-comments.component.html',
  styleUrls: ['./africlean-comments.component.scss'],
})
export class AfricleanCommentsComponent implements OnInit {

  public end = false;
  public author: any;

  /**
   * Liste des avis
   */
  @Input() public comments: {
    author: AfricleanUserModel,
    comment: AfricleanCommentModel
  }[];

  /**
   * Liste des avis temporaire
   */
  public commentsTmp = [];

  /**
   * La limite pour récuperer les avis
   */
  public limit = 0;

  /**
   * Verifie si tous les messages ont étés affichés
   */
  public hideSpinner = false;



  constructor(private alert: AfricleanAlertProvider,
              private commentProvider: AfricleanHttpCommentProvider,
              private session: AfricleanSessionProvider,
              private dateProvider: AfricleanDateProvider) {
   }

  ngOnInit() {
    this.comments = [];
    this.getAllComments();
  }

  /**
   * Charge les avis
   * @param event evenement emis lors du scroll
   */
  public loadDataOld(event: any) {
    setTimeout(() => {
      event.target.complete();
      this.moreData();
      // App logic to determine if all data is loaded
      // and disable the infinite scroll
      if (this.commentsTmp.length === this.comments.length) {
        event.target.disabled = true;
      }
    }, 1000);
  }

  /**
   * Charge les avis
   * @param event evenement emis lors du scroll
   */
  public loadData() {
    this.moreData();
    if (this.commentsTmp.length === this.comments.length) {
      this.end = true;
    }
  }

  /**
   * Permet d'ajouter un avis
   */
  public async addComment() {
    const message = 'COMPONENTS.AFRICLEAN_COMMENTS.ALERT.TITLE';
    const cssClass = '';
    const buttons = [
      {
        text: 'Fermer',
        role: 'close',
        handler: () => {
        }
      },
      {
        text: 'Publier',
        role: 'Publish',
        handler: () => {
        }
      }
    ];
    const inputs = [
      {
        name: 'comment',
        type: 'text',
        required: true,
        value: ''
      }
    ];
    const userId = await this.session.getItem('userId');
    this.alert.alertInput(message, cssClass, inputs, buttons).then(
      (res) => {
        if (res.data) {
          const comment = res.data.values.comment;
          if (res.role === 'Publish' && comment !== '') {
            const newComment = new AfricleanCommentModel();
            newComment.authorCode = userId;
            newComment.message = comment;
            this.saveComment(newComment).subscribe(
              (commentSaved) => {
                  // Réinitialiser la liste des commentaires 
                  this.comments = null;
                  this.commentsTmp = null;
                  this.hideSpinner = false;
                  this.limit = 0;
                  this.getAllComments();
              }
            );
          }
        }
      }
    );
  }

  /**
   * Ajouter plus de données (5 données)
   */
  private moreData() {
    const newArray = this.comments.slice(this.limit, this.limit + 5);
    this.commentsTmp = this.commentsTmp.concat(newArray);
    this.limit += 5;
  }


  /**
   * Enregistrer un commentaire
   * @param comment corps de la requête
   */
  private saveComment(comment: AfricleanCommentModel) {
    return this.commentProvider.save('comment/save', comment);
  }

  /**
   * Renvoi la liste de tous les commentaires
   */
  private getAllComments() {
    const now = new Date();
    this.commentProvider.getAll({ url: 'comment' }).subscribe(
        (comments) => {
          this.comments = comments;
          this.comments.forEach(comment => {
            const day = this.dateProvider.getDifference(comment.comment.dateComment, now);
            comment.comment.dateComment = this.dateProvider.getStructDate(day, comment.comment.dateComment);
          });
          this.commentsTmp = this.comments.slice(this.limit, this.limit + 5);
          this.limit += 5;
          this.hideSpinner = true;
        }
    );
  }

  /**
   * Renvoi la liste de tous les auteurs
   */
  private getAllAuthor() {
    this.commentProvider.getAllAuthor('comment/author').subscribe(
        (author) => {
          this.author = author;
        }
    );
  }


}
