import { Component, OnInit, Input } from '@angular/core';
import { AfricleanNotificationModel } from 'src/models/abonnement/africlean-notification.model';
import { AfricleanDateProvider } from 'src/providers/africlean-date/africlean-date.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanHttpNotificationProvider } from 'src/providers/africlean-http/africlean-http-notification.provider';
import { AfricleanPopoverProvider } from 'src/providers/africlean-popover/africlean-popover.provider';

@Component({
  selector: 'notification-item',
  templateUrl: './notification-item.component.html',
  styleUrls: ['./notification-item.component.scss'],
})
export class NotificationItemComponent implements OnInit {

  /**
   * Notification à afficher
   */
  @Input() notificationStruct: {
    notification: AfricleanNotificationModel,
    state: boolean
  };

  constructor(private dateProvider: AfricleanDateProvider,
              private session: AfricleanSessionProvider,
              private notificationProvider: AfricleanHttpNotificationProvider,
              private popover: AfricleanPopoverProvider) { }

  ngOnInit() {
      // Change le format de la date
      this.changeFormat();
  }

  /**
   * Change le format de la date
   */
  private changeFormat() {
    const now = new Date();
    const day = this.dateProvider.getDifference(this.notificationStruct.notification.dateOfSend, now);
    this.notificationStruct.notification.dateOfSend = this.dateProvider.getStructDate(day, now);
  }

  public async read() {
    const role = await this.session.getItem('role');
    if (this.notificationStruct.notification.title === 'NOUVEAU CLIENT' && 
        role === 'MANAGER') {
      // Rédiriger vers la page Abonnement
    }
  }

  /**
   * Fermer la notification
   */
  public async close() {
    const url = 'notification/hiden-notification';
    const userId = await this.session.getItem('userId');
    const body = {
        userCode: userId,
        notificationCode: this.notificationStruct.notification.objectId
    };
    console.log(body)
    this.notificationProvider.hiden(url, body).subscribe(
      (notificationUser) => {
        console.log(notificationUser);
        // TODOD : Utiliser l'idNotification pour afficher seulement les notifs qui ne matchent pas avec
        // cet id
        this.popover.dismissPopover();
      }
    );
  }
}
