import { Component, OnInit } from '@angular/core';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanUserModel } from 'src/models/africlean-user.model';
import { AfricleanTranslateProvider } from 'src/providers/africlean-translate/africlean-translate.provider';
import { AfricleanControlAuthenticationProvider } from 'src/providers/africlean-control-fields/africlean-control-authentication.provider';
import { AfricleanControlRegistrationProvider } from 'src/providers/africlean-control-fields/africlean-control-registration.provider';

@Component({
  selector: 'africlean-password',
  templateUrl: './africlean-password.component.html',
  styleUrls: ['./africlean-password.component.scss'],
})
export class AfricleanPasswordComponent implements OnInit {

  /**
   * L'utilisateur qui se connecte
   */
  public user: AfricleanUserModel;
  
  /**
   * Données à transmettre au serveur
   */
  public data: {
    userId: string,
    phoneNumber: string,
    role: string,
    password: string,
    newPassword: string
  };

  /**
   * Message d'erreur
   */
  public errorMsg: string;

  /**
   * Type d'erreur
   */
  public errorType: string;

  /**
   * Stock la confirmation du mot de passe
   */
  public confirmPassword: string;

  /**
   * Mot de passe courant
   */
  public currentPassword: string;

  constructor(private userProvider: AfricleanHttpUserProvider,
              private session: AfricleanSessionProvider,
              private translate: AfricleanTranslateProvider,
              private controlFields: AfricleanControlRegistrationProvider) { }

  ngOnInit() {
    // Récupérer le user session
    this.session.getItem('user').then(
      (user) => {
          this.user = user;
          this.session.getItem('role').
          then(
            (userRole) => {
              // initialisation de l'objet data
              this.data = {
                userId: this.user.objectId,
                phoneNumber: this.user.phoneNumber,
                role: userRole,
                password: '',
                newPassword: '',
              };
              this.confirmPassword = '';
              this.currentPassword = this.user.password;
            }
          );
      }
    );
  }

  /**
   * Modifie le mot de passe
   */
  public async update() {

    const url = 'user/update-password';
    const userLogin = await this.session.getItem('login');
    const userRole = await this.session.getItem('role');
    const body = {
            login: userLogin,
            password: this.data.password,
            role: userRole
    };
    this.userProvider.login('login/login-user', body.login, body.password).subscribe(
      (login) => {
          if (login.length !== 0) {
              if (this.controlPass(this.data)) {
                this.updatePass(url);
              }
          } else {
            this.errorType = 'error';
            this.errorMsg = this.translate.getMessageInstant('COMPONENTS.AFRICLEAN_PASSWORD.PASSWORD.NOT_EXIST');
          }
      }
    );
  }

  /**
   * Modifier le mot de passe
   * @param url url
   */
  private updatePass(url) {
    // Envoyé le mot de passe courant
    this.data.password = this.currentPassword;
    this.userProvider.updatePassword(url, this.data).subscribe(
      (userUpdated) => {
          // mettre à jour la session
          this.session.setItem('user', userUpdated);
          this.session.setItem('password', userUpdated.password);
          this.errorType = 'validate';
          this.errorMsg = this.translate.getMessageInstant('COMPONENTS.AFRICLEAN_PASSWORD.PASSWORD.VALIDATE');
      }
    );
  }

  /**
   * Contrôle la conformité de la confirmation mot de passe
   * @param url url
   */
  private controlPass(data: {
    userId: string,
    phoneNumber: string,
    role: string,
    password: string,
    newPassword: string
  }) {
      if (this.controlFields.controlPassword(data.newPassword)) {
        if (this.controlFields.controlConfirmPassword(this.confirmPassword)) {
          return true;
        }
      }
      this.errorType = 'error';
      this.errorMsg = this.translate.getMessageInstant('COMPONENTS.AFRICLEAN_PASSWORD.PASSWORD.NOT_CONFORM');
      return false;
  }

  /**
   * recupère la valeur du champ
   * @param field champ
   * @param event évenement émis
   */
  getData(field: any, event) {
    this.data[field] = event.detail.value;
  }

}
