import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'africlean-item',
  templateUrl: './africlean-item.component.html',
  styleUrls: ['./africlean-item.component.scss'],
})
export class AfricleanItemComponent implements OnInit {

  /**
   * Type de theme (abonnement,employes....)
   */
  @Input() public theme: string;

  /**
   * Texte de l'item
   */
  @Input() public title: string;

  /**
   * Sous texte de l'item
   */
  @Input() public subTitle: string;

  /**
   * Si le mois à été payer ou non => Abonnement
   */
  @Input() public isPaid: boolean;

  /**
   * Texte de l'item
   */
  @Output() public clickItem = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {}

}
